'use client'

import { useEffect, useState } from 'react'
import { motion, useScroll } from 'framer-motion'

const sections = [
  { id: 'hero', name: 'Intro', threshold: 0 },
  { id: 'experience', name: 'Resume', threshold: 0.22 },
  { id: 'work', name: 'Work', threshold: 0.35 },
  { id: 'art', name: 'Play', threshold: 0.84 },
  { id: 'contact', name: 'Contact', threshold: 0.93 },
]

export default function ProgressBar() {
  const { scrollYProgress } = useScroll()
  const [activeSection, setActiveSection] = useState(0)
  const [isVisible, setIsVisible] = useState(false)
  const [isLightMode, setIsLightMode] = useState(false)

  useEffect(() => {
    const unsubscribe = scrollYProgress.on('change', (latest) => {
      // Show progress bar after a small scroll
      setIsVisible(latest > 0.01)
      
      // Switch to light mode after hero section (around 20% scroll)
      setIsLightMode(latest > 0.18)
      
      // Determine active section
      for (let i = sections.length - 1; i >= 0; i--) {
        if (latest >= sections[i].threshold) {
          setActiveSection(i)
          break
        }
      }
    })

    return () => unsubscribe()
  }, [scrollYProgress])

  const scrollToSection = (threshold: number) => {
    const scrollHeight = document.documentElement.scrollHeight - window.innerHeight
    window.scrollTo({
      top: threshold * scrollHeight,
      behavior: 'smooth'
    })
  }

  return (
    <motion.div
      className="hidden lg:block fixed top-8 left-[100px] z-[100]"
      initial={{ opacity: 0, y: -20 }}
      animate={{ 
        opacity: isVisible ? 1 : 0, 
        y: isVisible ? 0 : -20 
      }}
      transition={{ duration: 0.3 }}
    >
      {/* Liquid glass container */}
      <motion.div 
        className="flex items-center gap-1 px-2 py-2 rounded-full transition-all duration-500"
        animate={{
          background: isLightMode 
            ? 'linear-gradient(135deg, rgba(0,0,0,0.05) 0%, rgba(0,0,0,0.02) 100%)'
            : 'linear-gradient(135deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%)',
          border: isLightMode 
            ? '1px solid rgba(0,0,0,0.1)'
            : '1px solid rgba(255,255,255,0.15)',
          boxShadow: isLightMode
            ? '0 8px 32px rgba(0,0,0,0.08), inset 0 1px 0 rgba(255,255,255,0.5)'
            : '0 8px 32px rgba(0,0,0,0.3), inset 0 1px 0 rgba(255,255,255,0.1)',
        }}
        style={{
          backdropFilter: 'blur(20px)',
          WebkitBackdropFilter: 'blur(20px)',
        }}
      >
        {sections.map((section, index) => {
          const isActive = index === activeSection
          const isPast = index < activeSection
          
          return (
            <div key={section.id} className="flex items-center">
              {/* Section button with dot and label */}
              <button
                onClick={() => scrollToSection(section.threshold)}
                className="flex items-center gap-2 px-3 py-1.5 rounded-full transition-all duration-300 group"
                style={{
                  background: isActive 
                    ? isLightMode
                      ? 'linear-gradient(135deg, rgba(180,130,60,0.2) 0%, rgba(140,90,40,0.15) 100%)'
                      : 'linear-gradient(135deg, rgba(20,184,166,0.3) 0%, rgba(16,185,129,0.2) 100%)'
                    : 'transparent',
                }}
              >
                {/* Dot indicator */}
                <div 
                  className={`w-2 h-2 rounded-full transition-all duration-300 ${
                    isActive 
                      ? isLightMode
                        ? 'bg-amber-600 shadow-[0_0_8px_rgba(180,130,60,0.6)]'
                        : 'bg-teal-400 shadow-[0_0_8px_rgba(20,184,166,0.6)]' 
                      : isPast 
                        ? isLightMode
                          ? 'bg-amber-600/60'
                          : 'bg-teal-400/60' 
                        : isLightMode
                          ? 'bg-black/20 group-hover:bg-black/40'
                          : 'bg-white/30 group-hover:bg-white/50'
                  }`}
                />
                
                {/* Label */}
                <span 
                  className={`font-sans text-xs whitespace-nowrap transition-all duration-300 ${
                    isActive 
                      ? isLightMode
                        ? 'text-text-dark font-medium'
                        : 'text-white font-medium' 
                      : isPast
                        ? isLightMode
                          ? 'text-text-dark/60'
                          : 'text-white/60'
                        : isLightMode
                          ? 'text-text-dark/40 group-hover:text-text-dark/60'
                          : 'text-white/40 group-hover:text-white/60'
                  }`}
                >
                  {section.name}
                </span>
              </button>

              {/* Connector line (except for last item) */}
              {index < sections.length - 1 && (
                <div 
                  className={`w-4 h-[1px] transition-all duration-300 ${
                    isPast 
                      ? isLightMode
                        ? 'bg-amber-600/60'
                        : 'bg-teal-400/60' 
                      : isLightMode
                        ? 'bg-black/15'
                        : 'bg-white/20'
                  }`}
                />
              )}
            </div>
          )
        })}
      </motion.div>
    </motion.div>
  )
}

